@import '../../theme';

.navButtons {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: $spacing;

    a {
        color: $white;
        text-transform: uppercase;
        margin-right: 16px;
        text-decoration: none;
        font-weight: bold;
        font-size: 14px;
    }
    
    .active{
        text-decoration: underline;
    }
  }